


















































































import { Vue, Component } from "vue-property-decorator";
import { State, Mutation } from "vuex-class";
@Component
export default class OnlineRegistration extends Vue {
  @Mutation("Loader/setOverLay") setOverLay!: Function;
  private search: string = "";
  private headers: Object = [
    { text: "掛號順序", value: "order" },
    { text: "身分證號碼", value: "identifier" },
    { text: "姓名", value: "name" },
    { text: "出生年月日", value: "birthDate" },
    { text: "掛號日期", value: "registrationDate" }
  ];
  private loading: Boolean = true;

  private totleRegistration: string = "今日掛號人數：";
  private nowRegistration: string = "目前看診進度：";

  private singleSelect: Boolean = false;
  private desserts: Array<Object> = [];
  getCurrentRegistrationAddDate() {
    var tzoffset = new Date().getTimezoneOffset() * 60000;
    this.desserts = [];
    this.loading = true;
    var currentDate = new Date(Date.now() - tzoffset)
      .toISOString()
      .substr(0, 10);
    var api = "/registration?date=" + currentDate;
    this.axios
      .get(api)
      .then(data => data.data)
      .then(({ registrations }) => {
        this.desserts = registrations;
        this.loading = false;
      })
      .catch(data => {
        this.$toasted.show(`資料讀取失敗，請重新整理`, {
          type: "error",
          position: "top-right",
          duration: 3000
        });
      });
  }
  process() {
    if (this.singleSelect) {
      this.getCurrentRegistrationAddDate();
    } else {
      this.desserts = [];
      this.loading = true;
      this.getRegistrationAll();
    }
  }
  created() {
    this.getCurrentRegistrationInfo();
    this.getRegistrationAll();
  }

  getRegistrationAll() {
    this.axios
      .get("/registration")
      .then(data => data.data)
      .then(({ registrations }) => {
        this.desserts = registrations;
        this.loading = false;
      })
      .catch(data => {
        this.$toasted.show(`資料讀取失敗，請重新整理`, {
          type: "error",
          position: "top-right",
          duration: 3000
        });
      });
  }
  getCurrentRegistrationInfo() {
    this.axios
      .get("/registration/order")
      .then(data => data)
      .then(({ data }) => {
        this.totleRegistration += data.total;
        this.nowRegistration += data.order;
      })
      .catch(data => {
        this.$toasted.show(`資料讀取失敗，請重新整理`, {
          type: "error",
          position: "top-right",
          duration: 3000
        });
      });
  }
  nextNum() {
    if (confirm("確定要跳到下一個號碼嗎?")) {
      this.setOverLay(true);
      this.axios
        .get("/registration/next")
        .then(data => data.data)
        .then(({ registrations }) => {
          if (this.singleSelect) {
            this.getCurrentRegistrationAddDate();
          } else {
            this.getRegistrationAll();
          }
          this.$toasted.show(`跳號成功`, {
            type: "success",
            position: "top-right",
            duration: 3000
          });
          this.setOverLay(false);
        })
        .catch(data => {
          this.$toasted.show(`跳號失敗請重新跳號`, {
            type: "error",
            position: "top-right",
            duration: 3000
          });
        });
    }
  }
  skipNum() {
    if (confirm("確定要過號嗎?")) {
      this.setOverLay(true);
      this.axios
        .get("/registration/skip")
        .then(data => data.data)
        .then(({ registrations }) => {
          if (this.singleSelect) {
            this.getCurrentRegistrationAddDate();
          } else {
            this.getRegistrationAll();
          }

          this.$toasted.show(`跳號成功`, {
            type: "success",
            position: "top-right",
            duration: 3000
          });
          this.setOverLay(false);
        })
        .catch(data => {
          this.$toasted.show(`跳號失敗請重新跳號`, {
            type: "error",
            position: "top-right",
            duration: 3000
          });
        });
    }
  }
}
